
.music {
	height: 75px;
	width: 100%;
	background: rgba(255,255,255,0.1);
	color: white;
	text-align: center;
	position: relative;
	/*overflow: hidden;*/
	z-index: 999;
}
.music-my {
	position: absolute;
	top: 75px;
	width:700px;
	height: 610px;
	left: -200%;
	margin-left: -350px;
	background: rgba(110, 168, 245, 0.8);
	transition: all .6s ease-out;
	font-family: 'KaiTi';
	color:white;
	overflow: hidden;
}
.music-myname {
	font-size: 24px;
	width: 100%;
	padding: 0 5%;
	/*margin:0 auto;*/
	height: 40px;
	line-height: 40px;
	box-sizing: border-box;
	background: rgb(25,60,120);
	position: relative;
	z-index: 99;
	font-family: 'KaiTi';
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently*/
}
.music-begin {
	float: left;
	margin-left: 8%;
	cursor: pointer;
	font-family: 'KaiTi';
}
.music-begin a {
	display: none;
}
.music-No {
	width: 35px;
	position: absolute;
	left: 10px;
}
.music-down {
	float: left;
	position: absolute;
	right: 21px;
}
.music-down a {
	color: rgba(255,255,255,0.7);
}
.music-down a:hover {
	color: orange;
}
.music-line {
	position: absolute;
	right: 60px;
	color: white!important;
}
.music-begin:hover {
	color:orange!important;
}
.music-text {
	float: left;
	width:295px;
	height:100%;
	text-align: center;
}
.music-master {
	float: left;
	width:200px;
	height:100%;
	text-align: center;
	margin-left: 2%;
}
.music-ul {
	width: 700px;
	height: auto;
	/*overflow: hidden;*/
	position: relative;
	z-index: 10;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently*/
	/*box-sizing: border-box;*/
}
.music-scroll {
	position: absolute;
	width:15px;
	height:570px;
	right:0px;
	top:40px;
	background: #eaeaea;
	z-index: 99;
}
.music-scrollbar {
	position: absolute;
	width:100%;
	height:80px;
	top:0;
	left:0;
	background: rgba(55, 114, 246,0.8);
	cursor: pointer;
	transition: color .3s;
}
.music-scrollbar:hover {
	background: rgba(55, 114, 246,0.9);
}
.music-li {
	width:100%;
	height:40px;
	line-height: 40px;
	font-size: 16px;
	padding-left: 30px;
	position: relative;
	box-sizing: border-box;
	font-family: 'KaiTi';
}
.music audio {
	width:300px;
	height:60px;
}
.music-wrap {
	width:900px;
	height:40px;
	position: absolute;
	top:50%;
	left:50%;
	margin-left: -450px;
	margin-top: -20px;
}
.music-btn {
	width: 135px;
	height:100%;
	float: left;
}
.music .c-btn {
	background: url('../assets/player.png');
	display: block;
	float: left;
	width:19px;
	height:20px;
	margin-top: 10px;
	cursor: pointer;
	opacity: 0.8;
	transition: opacity .2s;
	/*position: absolute;*/
}
.music .c-btn:hover {
	opacity: 1;
}
.music .prev {
	background-position: 0 -30px;
	margin-right: 32px;
}
.music .play {
	width:21px;
	height:29px;
	background-position: 0 0;
	margin-top: 6px;
	margin-right: 30px;
}
.music .pause {
	width:21px;
	height:29px;
	background-position: -30px 0;
	margin-top: 6px;
	margin-right: 30px;
}
.music .next {
	background-position: 0 -52px;
}
.music-show {
	width:450px;
	height:100%;
	float: left;
	margin-left: 25px;
	margin-top: -5px;
	/*position: relative;*/
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.music-name {
	font-size: 18px;
	text-align: left;
	margin-bottom: 5px;
	bottom:5px;
	font-family: 'KaiTi';
	position: relative;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently*/
}
.music-time {
	position: absolute;
	right:0;
	top:0;
}
.music-time span {
	font-family: '楷体';
	font-size: 18px;
}
.music-bar {
	width:100%;
	height:2px;
	bottom:-5px;
	background: rgba(135, 129, 130,0.8);
	position: relative;
	cursor: pointer;
}
.music-plan {
	position: absolute;
	width:0;
	height:2px;
	background: white;
}
.music-drag {
	position: absolute;
	width:12px;
	height:12px;
	border-radius: 50%;
	background: white;
	left:0;
	top:-5px;
	cursor: pointer;
}
.music-order {
	float: left;
	height:100%;
	width:35px;
	margin-left: 25px;
}
.music-order .order-play {
	display: block;
	width:23px;
	height:20px;
	background-position: 0 -260px;
}
.music-order .random-play {
	display: block;
	width:25px;
	height:19px;
	background-position: 0 -74px;
}
.music-order .one-play {
	display: block;
	width:26px;
	height:25px;
	background-position: 0 -232px;
}
.music-order .list-play {
	display: block;
	width:26px;
	height:25px;
	background-position: 0 -205px;
}
.music-vol {
	width:130px;
	height:100%;
	float: left;
	margin-left: 10px;
	margin-top: -1px;
}
.music-vol .vol-btn {
	width:26px;
	height:21px;
	background-position: 0 -144px;
	margin-top: 12px;
	float: left;
}
.music-vol .vol-close {
	width:26px;
	height:21px;
	background-position: 0 -182px;
	margin-top: 12px;
	float: left;
}
.music-vol .vol-bar {
	position: relative;
	width:90px;
	height:2px;
	background: rgba(135, 129, 130,0.8);
	float: left;
	margin-top: 20px;
	margin-left: 5px;
	cursor: pointer;
}
.vol-bar .vol-plan {
	width:90px;
	height:2px;
	background-color: white;
	position: absolute;
}
.music-vol .vol-bar-btn {
	position: absolute;
	width:12px;
	height:12px;
	border-radius: 50%;
	background: white;
	top:-5px;
	left:90px;
	cursor: pointer;
}
.music-list {
	width:59px;
	height:36px;
	float: left;
	margin-left: 20px;
}
.music-list-img {
	background: url('../assets/playbar.png');
	background-position: -42px -68px;
	float: none;
	width:38px;
	text-align: center;
	color:rgba(255,255,255,0.8);
	display: block;
	height:25px;
	margin: 5px 0 0 10px;
	padding-left: 21px;
	font-size: 15px;
	line-height: 27px;
	cursor: pointer;
	transition: color .2s;
	font-family: 'KaiTi';
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently*/
}
.music-list-img:hover {
	 background-position: -42px -98px;
	 color: white;
}



