
body {
  font-family: 'KaiTi';
  height: 100%;
  width: 100%;
  background: url('../static/assets/bg2.jpg') no-repeat;
  background-size: cover;
  overflow-y: hidden;
  overflow-x: hidden;
  position: relative;
}
#app {
  width: 100%;
  height: 100%;
}
.v-btn {
  position: fixed;
  bottom: 30px;
  width: 360px;
  height: 18px;
  left: 50%;
  margin-left: -180px;
   /*background: rgba(255,255,255,0.5);*/
}
.v-btn ul li {
  float: left;
  width: 56px;
  height: 7px;
  background: rgba(255,255,255,0.3);
  margin: 5px 8px ;
  border-radius: 5px;
}
.v-btn ul li a {
  width: 56px;
  height: 7px;
  display: block;
  transition: all .3s;
  border-radius: 5px;
}
.tab-view {
  position: absolute;
  transition: all .6s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  transform: translate(100px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  transform: translate(-100px, 0);
}



