
.hobby {
		width: 1200px;
		height: 640px;
		position: absolute;
		top: 150px;
		left: 50%;
		margin-left: -600px;
}
.hobby .ho {
		width: 290px;
		padding: 10px 5px;
		float: left;
		margin: 25px 10px 0 30px;
		border-radius: 10px;
}
.hobby .one {
		margin-left: 105px;
}
.hobby .ho .icon {
		width: 210px;
		height: 210px;
		background: white;
		margin-left: 40px;
		margin-top: 30px;
		border-radius: 50%;
		position: relative;
		margin-bottom: 20px;
}
.hobby .ho .icon:hover  .circle{
		transform: rotate(180deg);
		-webkit-transform: rotate(180deg);
		-moz-transform: rotate(180deg);
}
.hobby .ho .circle {
		width: 210px;
		height: 210px;
		animation: headneon 1.5s ease-in-out infinite alternate;
		opacity: 0.8;
		border-radius: 50%;
		position: absolute;
		top: -10px;
		left: -10px;
		transition: all 1s;
}
.hobby .ho .c_1 {
		border: 10px solid #6ADAFF;
		border-right-color: #65FF65;
		border-bottom-color: #65FF65;
}
.hobby .ho .c_2 {
		border: 10px solid rgb(70, 70, 71);
		border-right-color: #f46464;
		border-bottom-color: #f46464;
}
.hobby .ho .c_3 {
		border: 10px solid #6ADAFF;
		border-right-color: #FECE41;
		border-bottom-color: #FECE41;
}
.hobby .one {
		background-color: rgba(0,120,160,0.6);
}
.hobby .two {
		background-color: rgba(116, 113, 117, 0.88);
}
.hobby .three {
		background-color: rgba(30,190,140,0.6);
}
.hobby .ho p {
		width: 250px;
		font-size: 22px;
		color: white;
		text-indent: 2em;
		padding: 0.2em 0;
		margin-left: 22px;
		padding-bottom: 0;
}
.hobby .head {
		font-size: 36px;
		text-align: center;
		font-weight: 600;
		color: white;
}
.hobby .icon img {
		width: 210px;
		height: 210px;
		border-radius: 50%;
}
.hobby .icon a {
		width: 230px;
		height: 230px;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 50%;
}



