
.show {
  font-family: '楷体';
  width: 100%;
  height: 100%;
  color: white;
  font-size: 50px;
  text-align: center;
  /*background: rgba(255,255,255,0.2);*/
  position: relative;
}
.show-wrap {
  width: 1160px;
  height: 70%;
  /*background: rgba(255, 255, 255, 0.5);*/
  margin: 20px auto;
}
.show-demo {
  width: 210px;
  height: 220px;
  background: rgba(255,255,255,0.6);
  border-radius: 5px;
  position: relative;
  float: left;
  margin-right: 20px;
  margin-top: 60px;
  /* margin-left: 30px; */
  overflow: hidden;
  transition: all .3s;
}
.show-demo:hover {
  background: rgba(255,255,255,0.9);
}
.show-demo:hover .demos {
  opacity: 1;
}
.show-demo:hover .demo-top {
	/*color:white;*/
}
.show-demo:hover .ico-span {
  transform: translate(0,83px);
}
.show-demo:hover .ico-line {
  transform: translate(0,0);
}
.demos {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.8);
  transition: all .3s;
  opacity: 0;
}
.ico-span {
  position: absolute;
  left: 50%;
  /* top: -20px; */
  margin-left: -27px;
  /* margin-top: -31px; */
  transform: translate(0,-50px);
  padding: 5px;
  display: inline-block;
  border: 2px solid white;
  border-radius: 50%;
  transition: all .3s;
}
.ico-span>a {
  position: absolute;
  top:0;
  left:0;
  right: 0;
  bottom:0;
}
.ico-span:hover {
  background: rgba(255, 255, 255, 0.5);
}
.ico-line {
  position: absolute;
  display: block;
  height: 84px;
  width: 2px;
  background: white;
  left: 50%;
  margin-left: -2px;
  border-radius: 2px;
  /* top: -134px; */
  transform: translate(0,-134px);
  transition: all .3s;
}
.icono-home {
  margin-bottom: 5px;
  margin-left: 8px;
  margin-right: 8px;
}
.show-demo .demo-type {
  font-size: 18px;
  line-height: 38px;
  padding-left: 6px;
}
.show-demo .demo-top {
  width: 100%;
  height: 22%;
  font-size: 16px;
  color: rgb(0,0,0);
  text-align: left;
  padding-left: 2px;
  box-sizing: border-box;
  padding-top: 2%;
  border-radius: 5px 5px 0 0;
  font-family: '楷体';
  opacity: 0.8;
}
.show-demo:nth-child(1) .demo-top {
  background: url('../assets/demo1.png');
}
.show-demo:nth-child(2) .demo-top {
  background: url('../assets/demo2.png');
}
.show-demo:nth-child(3) .demo-top {
  background: url('../assets/demo3.png');
}
.show-demo:nth-child(4) .demo-top {
  background: url('../assets/demo4.png');
}
.show-demo:nth-child(5) .demo-top {
  background: url('../assets/demo5.png');
}
.show-demo:nth-child(6) .demo-top {
  background: url('../assets/demo6.png');
}
.show-demo:nth-child(7) .demo-top {
  background: url('../assets/demo7.png');
}
.show-demo:nth-child(8) .demo-top {
  background: url('../assets/demo8.png');
}
.show-demo:nth-child(9) .demo-top {
  background: url('../assets/demo3.png');
}
.show-demo:nth-child(10) .demo-top {
  background: url('../assets/demo6.png');
}
.demo-wrap {
  width: 100%;
  height: 78%;
  box-sizing: border-box;
  padding: 5% 5%;
  color: black;
  font-size: 20px;
}



